import React, { useState, FormEvent } from 'react';
import {
  ChakraProvider,
  Box,
  Flex,
  Button,
  Input,
  InputGroup,
  InputLeftElement,
  Progress,
  Text,
  Checkbox,
  useToast,
} from '@chakra-ui/react';
import { 
  ArrowLeft, 
  User, 
  Mail, 
  Lock,
  BrandGoogle,
  BrandFacebook,
  BrandApple 
} from 'tabler-icons-react';
import { kStyleGlobal } from '../theme/theme';
import { useNavigate } from 'react-router-dom';
import { supabase } from '../lib/supabase';

interface FormData {
  username: string;
  email: string;
  password: string;
  confirmPassword: string;
  agreeToTerms: boolean;
}

interface PasswordStrength {
  score: number;
  message: string;
}

const RegisterScreen: React.FC = () => {
  const navigate = useNavigate();
  const toast = useToast();
  
  const [formData, setFormData] = useState({
    username: '',
    email: '',
    password: '',
    confirmPassword: '',
    agreeToTerms: false
  });

  const [passwordStrength, setPasswordStrength] = useState({
    score: 0,
    message: ''
  });

  const checkPasswordStrength = (password: string) => {
    let score = 0;
    if (password.length >= 8) score += 25;
    if (password.match(/[A-Z]/)) score += 25;
    if (password.match(/[0-9]/)) score += 25;
    if (password.match(/[^A-Za-z0-9]/)) score += 25;

    let message = '';
    if (score >= 75) message = '強';
    else if (score >= 50) message = '中';
    else message = '弱';

    setPasswordStrength({ score, message });
  };

  const handleInputChange = (field: keyof FormData, value: string | boolean) => {
    setFormData(prev => ({ ...prev, [field]: value }));
    if (field === 'password' && typeof value === 'string') {
      checkPasswordStrength(value);
    }
  };

  const handleRegister = async () => {
    if (!formData.agreeToTerms) {
      toast({
        title: '請同意用戶協議和隱私政策',
        status: 'error',
        duration: 3000,
      });
      return;
    }

    if (formData.password !== formData.confirmPassword) {
      toast({
        title: '密碼不匹配',
        status: 'error',
        duration: 3000,
      });
      return;
    }

    try {
      const { error } = await supabase.auth.signUp({
        email: formData.email,
        password: formData.password,
        options: {
          data: {
            username: formData.username,
          }
        }
      });

      if (error) throw error;

      toast({
        title: '註冊成功',
        status: 'success',
        duration: 3000,
      });
      navigate('/');
    } catch (error) {
      toast({
        title: '註冊失敗',
        description: error instanceof Error ? error.message : '未知錯誤',
        status: 'error',
        duration: 3000,
      });
    }
  };

  return (
    <ChakraProvider theme={kStyleGlobal}>
      <Flex
        direction="column"
        minH="100vh"
        bg="gray.50"
      >
        <Box
          px={6}
          py={8}
          bg="white"
        >
          <Flex
            align="center"
            mb={8}
          >
            <Button
              variant="ghost"
              onClick={() => navigate(-1)}
              mr={3}
            >
              <ArrowLeft
                size={24}
                color={kStyleGlobal.colors.gray[700]}
              />
            </Button>
            <Text
              fontSize="24px"
              fontWeight="700"
              color={kStyleGlobal.colors.gray[900]}
            >
              註冊
            </Text>
          </Flex>

          <form onSubmit={(e: FormEvent<HTMLFormElement>) => {
            e.preventDefault();
            handleRegister();
          }}>
            <Flex direction="column" gap={6}>
              {/* Username Input */}
              <Flex direction="column" gap={2}>
                <Text
                  fontSize="14px"
                  fontWeight="600"
                  color={kStyleGlobal.colors.gray[700]}
                >
                  用戶名
                </Text>
                <InputGroup>
                  <InputLeftElement
                    pointerEvents="none"
                    children={<User size={20} color="gray.400" />}
                  />
                  <Input
                    type="text"
                    placeholder="請輸入用戶名"
                    bg="gray.50"
                    border="none"
                    h="54px"
                    fontSize="md"
                    pl={12}
                    value={formData.username}
                    onChange={(e) => handleInputChange('username', e.target.value)}
                  />
                </InputGroup>
              </Flex>

              {/* Email Input */}
              <Flex direction="column" gap={2}>
                <Text
                  fontSize="14px"
                  fontWeight="600"
                  color={kStyleGlobal.colors.gray[700]}
                >
                  電子郵箱
                </Text>
                <InputGroup>
                  <InputLeftElement
                    pointerEvents="none"
                    children={<Mail size={20} color="gray.400" />}
                  />
                  <Input
                    type="email"
                    placeholder="請輸入電子郵箱"
                    bg="gray.50"
                    border="none"
                    h="54px"
                    fontSize="md"
                    pl={12}
                    value={formData.email}
                    onChange={(e) => handleInputChange('email', e.target.value)}
                  />
                </InputGroup>
              </Flex>

              {/* Password Input */}
              <Flex direction="column" gap={2}>
                <Text
                  fontSize="14px"
                  fontWeight="600"
                  color={kStyleGlobal.colors.gray[700]}
                >
                  密碼
                </Text>
                <InputGroup>
                  <InputLeftElement
                    pointerEvents="none"
                    children={<Lock size={20} color="gray.400" />}
                  />
                  <Input
                    type="password"
                    placeholder="至少8位字符"
                    bg="gray.50"
                    border="none"
                    h="54px"
                    fontSize="md"
                    pl={12}
                    value={formData.password}
                    onChange={(e) => handleInputChange('password', e.target.value)}
                  />
                </InputGroup>
                <Progress
                  value={passwordStrength.score}
                  size="xs"
                  colorScheme={passwordStrength.score >= 75 ? "green" : passwordStrength.score >= 50 ? "yellow" : "red"}
                  borderRadius="full"
                  mt={2}
                />
                <Text
                  fontSize="12px"
                  color={passwordStrength.score >= 75 ? "green.500" : passwordStrength.score >= 50 ? "yellow.500" : "red.500"}
                  mt="4px"
                >
                  密碼強度：{passwordStrength.message}
                </Text>
              </Flex>

              {/* Confirm Password Input */}
              <Flex direction="column" gap={2}>
                <Text
                  fontSize="14px"
                  fontWeight="600"
                  color={kStyleGlobal.colors.gray[700]}
                >
                  確認密碼
                </Text>
                <InputGroup>
                  <InputLeftElement
                    pointerEvents="none"
                    children={<Lock size={20} color="gray.400" />}
                  />
                  <Input
                    type="password"
                    placeholder="請再次輸入密碼"
                    bg="gray.50"
                    border="none"
                    h="54px"
                    fontSize="md"
                    pl={12}
                    value={formData.confirmPassword}
                    onChange={(e) => handleInputChange('confirmPassword', e.target.value)}
                  />
                </InputGroup>
              </Flex>

              {/* Terms Checkbox */}
              <Checkbox
                colorScheme="gray"
                size="md"
                mt={2}
                isChecked={formData.agreeToTerms}
                onChange={(e) => handleInputChange('agreeToTerms', e.target.checked)}
              >
                <Flex align="center" gap={1}>
                  <Text fontSize="14px" color={kStyleGlobal.colors.gray[600]}>
                    我已閱讀並同意
                  </Text>
                  <Text
                    fontSize="14px"
                    color="blue.500"
                    textDecoration="underline"
                    cursor="pointer"
                  >
                    用戶協議
                  </Text>
                  <Text fontSize="14px" color={kStyleGlobal.colors.gray[600]}>
                    和
                  </Text>
                  <Text
                    fontSize="14px"
                    color="blue.500"
                    textDecoration="underline"
                    cursor="pointer"
                  >
                    隱私政策
                  </Text>
                </Flex>
              </Checkbox>

              {/* Register Button */}
              <Button
                bg="black"
                color="white"
                size="lg"
                h="54px"
                fontSize="md"
                fontWeight="600"
                _hover={{
                  bg: "gray.800"
                }}
                mt={4}
                onClick={handleRegister}
              >
                註冊
              </Button>

              {/* Social Login Section */}
              <Flex
                direction="column"
                align="center"
                mt={8}
                gap={6}
              >
                <Text
                  fontSize="14px"
                  color={kStyleGlobal.colors.gray[500]}
                >
                  或通過以下方式註冊
                </Text>
                <Flex gap={4}>
                  <Button
                    variant="outline"
                    borderRadius="full"
                    w="50px"
                    h="50px"
                    borderColor="gray.200"
                    _hover={{
                      bg: "gray.50"
                    }}
                  >
                    <BrandGoogle size={24} color="gray.600" />
                  </Button>
                  <Button
                    variant="outline"
                    borderRadius="full"
                    w="50px"
                    h="50px"
                    borderColor="gray.200"
                    _hover={{
                      bg: "gray.50"
                    }}
                  >
                    <BrandFacebook size={24} color="gray.600" />
                  </Button>
                  <Button
                    variant="outline"
                    borderRadius="full"
                    w="50px"
                    h="50px"
                    borderColor="gray.200"
                    _hover={{
                      bg: "gray.50"
                    }}
                  >
                    <BrandApple size={24} color="gray.600" />
                  </Button>
                </Flex>

                {/* Login Link */}
                <Flex
                  align="center"
                  gap={1}
                  mt={4}
                >
                  <Text
                    fontSize="14px"
                    color={kStyleGlobal.colors.gray[600]}
                  >
                    已有帳號？
                  </Text>
                  <Button
                    variant="link"
                    color="blue.500"
                    fontWeight="600"
                    onClick={() => navigate('/login')}
                  >
                    立即登錄
                  </Button>
                </Flex>
              </Flex>
            </Flex>
          </form>
        </Box>
      </Flex>
    </ChakraProvider>
  );
};

export default RegisterScreen;
