import React, { useState } from 'react';
import {
  ChakraProvider,
  Box,
  Flex,
  Button,
  Input,
  InputGroup,
  InputLeftElement,
  InputRightElement,
  Stack,
  Text,
  Image,
  Checkbox,
  useToast
} from '@chakra-ui/react';
import { 
  Mail as IconMail,
  Lock as IconLock,
  Eye as IconEye,
  EyeOff as IconEyeOff,
  BrandGoogle as IconBrandGoogle,
  BrandFacebook as IconBrandFacebook,
  BrandApple as IconBrandApple
} from 'tabler-icons-react';
import { useNavigate } from 'react-router-dom';
import { kStyleGlobal } from '../theme/theme';
import { supabase } from '../lib/supabase';

const LoginScreen: React.FC = () => {
  const navigate = useNavigate();
  const toast = useToast();
  const [showPassword, setShowPassword] = useState(false);
  const [formData, setFormData] = useState({
    email: '',
    password: '',
    rememberMe: true
  });

  const handleLogin = async () => {
    try {
      const { error } = await supabase.auth.signInWithPassword({
        email: formData.email,
        password: formData.password,
      });

      if (error) throw error;

      toast({
        title: '登入成功',
        status: 'success',
        duration: 3000,
      });
      navigate('/onboarding');
    } catch (error) {
      toast({
        title: '登入失敗',
        description: error instanceof Error ? error.message : '未知錯誤',
        status: 'error',
        duration: 3000,
      });
    }
  };

  return (
    <Flex
      direction="column"
      minH="100vh"
      bg="gray.50"
      p={6}
    >
      <Flex
        direction="column"
        maxW="440px"
        mx="auto"
        w="100%"
      >
        <Flex
          justify="center"
          mb={10}
        >
          <Image
            src="https://images.unsplash.com/photo-1614027164847-1b28cfe1df60"
            h="48px"
            objectFit="contain"
            alt="Logo"
          />
        </Flex>
        <Box
          bg="white"
          borderRadius="2xl"
          p={8}
          boxShadow="0 4px 6px rgba(0, 0, 0, 0.05)"
          mb={6}
        >
          <Text
            fontSize="24px"
            fontWeight="700"
            textAlign="center"
            mb="32px"
          >
            登入
          </Text>
          <Stack spacing={6}>
            <InputGroup>
              <InputLeftElement
                pointerEvents="none"
                pl={4}
              >
                <IconMail size={20} color="gray" />
              </InputLeftElement>
              <Input
                pl={12}
                placeholder="電子郵件"
                size="lg"
                bg="gray.50"
                border="none"
                borderRadius="xl"
                value={formData.email}
                onChange={(e) => setFormData(prev => ({ ...prev, email: e.target.value }))}
              />
            </InputGroup>
            <InputGroup>
              <InputLeftElement
                pointerEvents="none"
                pl={4}
              >
                <IconLock size={20} color="gray" />
              </InputLeftElement>
              <Input
                pl={12}
                type={showPassword ? "text" : "password"}
                placeholder="密碼"
                size="lg"
                bg="gray.50"
                border="none"
                borderRadius="xl"
                value={formData.password}
                onChange={(e) => setFormData(prev => ({ ...prev, password: e.target.value }))}
              />
              <InputRightElement pr={4}>
                <Button
                  variant="ghost"
                  onClick={() => setShowPassword(!showPassword)}
                  size="sm"
                >
                  {showPassword ? <IconEyeOff size={20} color="gray" /> : <IconEye size={20} color="gray" />}
                </Button>
              </InputRightElement>
            </InputGroup>
            <Flex
              justify="space-between"
              align="center"
            >
              <Checkbox
                colorScheme="blue"
                isChecked={formData.rememberMe}
                onChange={(e) => setFormData(prev => ({ ...prev, rememberMe: e.target.checked }))}
              >
                <Text fontSize="14px">記住我</Text>
              </Checkbox>
              <Button
                variant="ghost"
                color="blue.500"
                onClick={() => navigate('/reset-password')}
              >
                <Text fontSize="14px">忘記密碼？</Text>
              </Button>
            </Flex>
            <Button
              bg="black"
              color="white"
              size="lg"
              h="54px"
              _hover={{
                bg: "gray.800"
              }}
              onClick={handleLogin}
            >
              登入
            </Button>
          </Stack>
        </Box>
        <Box
          bg="white"
          borderRadius="2xl"
          p={8}
          boxShadow="0 4px 6px rgba(0, 0, 0, 0.05)"
        >
          <Text
            textAlign="center"
            mb="24px"
            color="gray.600"
          >
            或使用以下方式繼續
          </Text>
          <Flex
            gap={4}
            justify="center"
          >
            <Button
              variant="outline"
              w="full"
              h="54px"
              borderColor="gray.200"
              _hover={{
                bg: "gray.50"
              }}
            >
              <IconBrandGoogle size={20} />
            </Button>
            <Button
              variant="outline"
              w="full"
              h="54px"
              borderColor="gray.200"
              _hover={{
                bg: "gray.50"
              }}
            >
              <IconBrandApple size={20} />
            </Button>
            <Button
              variant="outline"
              w="full"
              h="54px"
              borderColor="gray.200"
              _hover={{
                bg: "gray.50"
              }}
            >
              <IconBrandFacebook size={20} />
            </Button>
          </Flex>
        </Box>
        <Flex
          justify="center"
          mt={6}
          gap={1}
        >
          <Text color="gray.600">
            還沒有帳號？
          </Text>
          <Button
            variant="link"
            color="blue.500"
            fontWeight="semibold"
            onClick={() => navigate('/register')}
          >
            註冊
          </Button>
        </Flex>
        <Text
          fontSize="12px"
          color="gray.500"
          textAlign="center"
          mt="24px"
        >
          登入即表示您同意我們的服務條款和隱私政策
        </Text>
      </Flex>
    </Flex>
  );
};

export default LoginScreen;
